import { useState } from 'react';
import './CharacterCreator.styles.css';

const characters = ['test', 'best', 'foo', 'bar'];

const CharacterCreator = () => {
  const [characterId, setCharacterId] = useState(0);

  const getPreview = () => {
    if (characterId <= 0) {
      setCharacterId(characters.length - 1)  
    } else {
      setCharacterId(characterId - 1)
    }
  }
  const getNext = () => {
    if (characterId >= characters.length - 1) {
      setCharacterId(0)  
    } else {
      setCharacterId(characterId + 1)
    }
  }

  return (
    <>
      <div>{ characters[characterId] }</div>
      <div className={ 'character-window' }>
        <button
          onClick={ getPreview }
          className={ 'character-window__control character-window__control--left' }
        />
        <button
          onClick={ getNext }
          className={ 'character-window__control character-window__control--right' }
        />
      </div>
    </>
  )
};

export default CharacterCreator;
